<template><section><h1>SphereGeometry, SphereOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-sphere</code> 组件用于加载球体。</li>
<li><code v-pre>vc-geometry-outline-sphere</code> 组件用于加载球体轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载球体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer ref="viewer" @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive ref="primitive" :appearance="appearance">
          <vc-instance-geometry ref="geometry" :geometry.sync="geometry" :attributes="attributes" :modelMatrix="modelMatrix">
            <vc-geometry-sphere ref="sphere" :radius="radius" :vertexFormat="vertexFormat">
          </vc-geometry-sphere></vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes" :modelMatrix="modelMatrixOutline">
            <vc-geometry-outline-sphere ref="sphereOutline" :radius="radius"></vc-geometry-outline-sphere>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
