            <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-outline-sphere</span>
              <span class="hljs-attr">ref</span>=<span class="hljs-string">"sphereOutline"</span>
              <span class="hljs-attr">:radius</span>=<span class="hljs-string">"radius"</span>
            &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-outline-sphere</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
