          <span class="hljs-keyword">this</span>.appearance = <span class="hljs-keyword">new</span> PerInstanceColorAppearance({
            <span class="hljs-attr">flat</span> : <span class="hljs-literal">true</span>
          })
          <span class="hljs-keyword">this</span>.vertexFormat = PerInstanceColorAppearance.VERTEX_FORMAT
          <span class="hljs-keyword">this</span>.attributes = {
            <span class="hljs-attr">color</span>: <span class="hljs-keyword">new</span> ColorGeometryInstanceAttribute(<span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-number">0.5</span>)
          }
          <span class="hljs-keyword">this</span>.modelMatrix = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(<span class="hljs-number">105.0</span>, <span class="hljs-number">35.0</span>)),
            <span class="hljs-keyword">new</span> Cartesian3(<span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">100000</span>),
